@import url(https://fonts.googleapis.com/css2?family=Syne+Mono&display=swap);
.main {
  display: flex;
  margin: 0 auto;
}

.container {
  width: 100%;
  display: flex;
  padding: 8px;
  flex-direction: column;
}

.about-image-container {
  width: 100%; 
}

.image {
  border-radius: 15px;
  margin-right: 40px;
  display: block;
  max-width: 100%;
  width: auto;
  height: auto;
}

.about-title {
  text-align: center;
  margin-bottom: 8px;
}

.about-me {
  display: flex;
  padding-right: 40px;
  justify-content: center;
}

.about-image-container,
.about-text {
  flex-grow: 1;
  flex-basis: 0;
}

.about-text {
  padding: 16px;
}

.nav-tabs {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 8px 16px;
}

.nav-list { 
  display: flex;
  justify-content: space-between;
}


.logos {
  display: flex;
  height: 25%;
  background-color: var(--light);
  justify-content: space-around;
  padding: 40px;
}

.icon {
  width: 45px;
}

.form {
  display: flex;
  flex-direction: column;

}

.contact-me {
  width: 100%;
  display: flex;
  justify-content: center;
  
  align-items: center;
  
}

.button-submit {
  margin-top: 5px;
}

.resume-div {
  padding-left: 30px;
}

.projects {
  display: flex;
  
}

.card-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;

}

.card {
  display: flexbox;
  padding: 15px;
  border-radius: 10px;
  background-color: var(--light);
  justify-content: center;
  text-align: center;
  overflow: hidden;
  width: 50%;
  margin: 20px;

  
}

.card-body {
  font-size: 20px;
  flex-grow: 2;
  margin-left: 5px;
  display: flex;
  justify-content: space-between;
  flex-flow: column;
  background-color: var(--light);
  border-radius: 10px;
}

.card-img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  border: solid 1px black;
}

.port {
  margin-top: 10px;
  text-align: center;
}

.h1tag {
  align-items: center;
  justify-items: center;
}




@media screen and (max-width: 480px) {

  .about-me {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

 .image {
   margin: 16px;
 }


  .nav-title {
    white-space: nowrap;
  }

 .nav-list {
   overflow-x: auto;
   
 }

 .nav-item {
  margin: 4px;
 }

 h3 {
   font-size: 1.7rem;
 }

 p {
   font-size: 1rem;
   word-break: break-word ;
 }

}
:root {
    --primary: #03045E;
    --secondary: #0096C7;
    --tertiary: #90E0EF;
    --light: #fafafa;
    --dark: #000000;
    --darker: #111111;
    --error: rgb(228, 46, 1);
    --spacing-one: calc(1rem * 0.5);
    --spacing-two: calc(1rem);
    --spacing-five: calc(2.5rem);
  }
  
  * {
    box-sizing: border-box;
  }
  
  html,
  body {
    min-height: 100%;
  }
  
  body {
    margin: 0;
    padding: 0;
    font-weight: 600;
    line-height: 1.5;
    font-size: 18px;
    letter-spacing: .1em;
    background-color: #03045E;
    background-color: var(--primary);
    color: #fafafa;
    color: var(--light);
    font-family: Arial, Helvetica, sans-serif;
  
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-family: 'Roboto', sans-serif;
    font-weight: 500;
    margin: 5px 0;
    line-height: 1.25;
  }
  
  h1 {
    font-size: 2.25rem;
  }
  
  h2 {
    font-size: 2rem;
  }
  
  h3 {
    font-size: 1.75rem;
  }
  
  h4 {
    font-size: 1.5rem;
  }
  
  h5 {
    font-size: 1.25rem;
  }
  
  h6 {
    font-size: 1rem;
  }
  
  p {
    font-size: 1.1rem;
    
  }
  
  a {
    color: #fafafa;
    color: var(--light);
    text-decoration: none;
    font-weight: bold;
  }
  
  a:hover {
    text-decoration: underline;
  }
  
  header {
    background: #0096C7;
    background: var(--secondary);
  }
  
  header a {
    color: #fafafa;
    color: var(--light);
  }
  
  .button {
    font-size: 1rem;
    text-decoration: none;
    padding: 0.25rem 1rem;
    border-radius: .5rem;
    border: 1rem;
    outline: none;
    cursor: pointer;
    margin: 0 1px;
    background-color: #0096C7;
    background-color: var(--secondary);
  }
  
  button:hover {
    opacity: .8;
  }
  
  /*button[disabled] {
    cursor: default;
    opacity: .8;
  }*/
  
  img {
    max-width: 100%;
  }
  
  span[role="img"] {
    cursor: default;
  }
  
  form {
    width: 40%;
  }
  
  input {
    font-size: 1rem;
    padding: 0.25rem;
    border: 1px solid #000000;
    border: 1px solid var(--dark);
    outline: none;
    border-radius: 3px;
    transition: box-shadow 0.2s;
  }
  
  input:focus {
    border: 1px solid #0096C7;
    border: 1px solid var(--secondary);
    box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
  }
  
  li {
    list-style: none;
  }
  
  nav a:hover {
    text-decoration: none;
  }
  
  nav  li {
    cursor: pointer;
  }
  
  textarea {
      width: 600px;
      height: 120px;
      border: 3px solid #cccccc;
      padding: 5px;
  }
  
  .container {
    width: 85%;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
  }
  
  .flex-row {
    display: flex;
    flex-wrap: wrap;
  }
  
  .space-between {
    justify-content: space-between;
  }
  
  .flex-end {
    justify-content: flex-end;
  }
  
  .mx-1 {
    margin-right: calc(1rem * 0.5);
    margin-right: var(--spacing-one);
    margin-left: calc(1rem * 0.5);
    margin-left: var(--spacing-one);
  }
  
  .mx-2 {
    margin-right: calc(1rem);
    margin-right: var(--spacing-two);
    margin-left: calc(1rem);
    margin-left: var(--spacing-two);
  }
  
  .my-1 {
    margin-top: calc(1rem * 0.5);
    margin-top: var(--spacing-one);
    margin-bottom: calc(1rem * 0.5);
    margin-bottom: var(--spacing-one);
  }
  
  .my-2 {
    margin-top: calc(1rem);
    margin-top: var(--spacing-two);
    margin-bottom: calc(1rem);
    margin-bottom: var(--spacing-two);
  }
  
  .my-5 {
    margin-top: calc(2.5rem);
    margin-top: var(--spacing-five);
    margin-bottom: calc(2.5rem);
    margin-bottom: var(--spacing-five);
  }
  
  .px-1 {
    padding-right: calc(1rem * 0.5);
    padding-right: var(--spacing-one);
    padding-left: calc(1rem * 0.5);
    padding-left: var(--spacing-one);
  }
  
  .px-2 {
    padding-right: calc(1rem);
    padding-right: var(--spacing-two);
    padding-left: calc(1rem);
    padding-left: var(--spacing-two);
  }
  
  .py-1 {
    padding-top: calc(1rem * 0.5);
    padding-top: var(--spacing-one);
    padding-bottom: calc(1rem * 0.5);
    padding-bottom: var(--spacing-one);
  }
  
  .py-2 {
    padding-top: calc(1rem);
    padding-top: var(--spacing-two);
    padding-bottom: calc(1rem);
    padding-bottom: var(--spacing-two);
  }
  
  .card {
    
    text-align: center;
  }
  
  .img-thumbnail {
    width: auto;
    height: 300px;
    margin-bottom: 50px;
    cursor: pointer;
  }
  
  .modalBackdrop {
      background: rgba(0, 0, 0, .65);
      bottom: 0;
      left: 0;
      overflow: auto;
      position: fixed;
      right: 0;
      top: 0;
      z-index: 1;
  }
  
  .modalContainer {
      background: #111111;
      background: var(--darker);
      border-radius: 5px;
      max-width: 100%;
      margin: 50px auto;
      padding: 15px;
      width: 960px;
  }
  
  .modalTitle {
      font-size: 18px;
      font-weight: 600;
      margin-bottom: 25px;
  }
  
  button {
      background: #90E0EF;
      background: var(--tertiary);
      border: 0;
      border-radius: 5px;
      color: #03045E;
      color: var(--primary);
      cursor: pointer;
      font-size: 16px;
      font-weight: 600;
      margin-bottom: 30px;
      padding: 10px 15px;	
  }
  
  a {
      border-radius: 5px;
      color: #0096C7;
      color: var(--secondary);
      font-weight: 600;
      padding: 2px 4px;
      margin-left: -2px;
      margin-right: -2px;
      
  }
  
  .navActive {
    color: #0096C7;
    color: var(--secondary);
  }
.navbar {
    background-color: #0096C7;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
  }
  
  .menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
  }
  
  .nav-menu {
    background-color: #060b26;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
  }
  
  .nav-menu.active {
    left: 0;
    transition: 350ms;
  }
  
  .nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
  }
  
  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
  }
  
  .nav-text a:hover {
    background-color: #1a83ff;
  }
  
  .nav-menu-items {
    width: 100%;
  }
  
  .navbar-toggle {
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
  }
  
  span {
    margin-left: 16px;
  }
